.webVersion{
margin-top: 15vw;
width: 100%;
display: flex;
flex-direction: column;
align-items: center;
}

.piece{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
transition: .1s all;
}

.outline{
position: fixed;
-webkit-mask-box-image: url("../../assets/outline.png");
height: 15vw;
width: 45vw;
z-index: 4;
transition: background-color 300ms, height 500ms, width 500ms;
}

.fill{
position: fixed;
-webkit-mask-box-image: url("../../assets/fill.png");
height: 15vw;
width: 45vw;
z-index: 2;
transition: background-color 300ms, height 500ms, width 500ms;
}

.background{
position: fixed;
-webkit-mask-box-image: url("../../assets/background.png");
height: 15vw;
width: 45vw;
z-index: 1;
transition: background-color 300ms, height 500ms, width 500ms;
}

.highlights{
position: fixed;
-webkit-mask-box-image: url("../../assets/highlights.png");
height: 15vw;
width: 45vw;
z-index: 4;
transition: background-color 300ms, height 500ms, width 500ms;
}

.force{
position: fixed;
-webkit-mask-box-image: url("../../assets/powerline.png");
height: 15vw;
width: 45vw;
z-index: 2;
transition: background-color 300ms, height 500ms, width 500ms;
}

.whiteBackground{
position: fixed;
z-index: 0;
border-radius: 50px;
transition: 500ms;
background-color: rgba(255, 255, 255, 0.658);
width: 44vw;
height: 15vw;
}

.smaller{
height: 12vw;
width: 39vw;
transition: background-color 300ms, height 500ms, width 500ms;
}



// .settings{
// display: flex;
// flex-direction: column;
// justify-content: center;
