// width: 90%;

// }

.switchButtons{
display: flex;
flex-direction: row;
justify-content: space-evenly;
width: 90%;
margin: 2vh 0;
}

.switchButton{
font-size: 1.5vw;
padding: 1vh 2vw;
width: 15%;
background-color: #423f47;
color: white;
text-align: center;
border: 2px solid rgb(139, 139, 139);
box-shadow: rgb(0 0 0 / 60%) 1px 3px 2px;
border-radius: 50px;
cursor: pointer;
transition: 300ms;

&:active{
transform: scale(0.98);
transition: scale 300ms;
}
}

.switchButtonOff{
font-size: 1.5vw;
padding: 1vh 2vw;
width: 15%;
background-color: #3d3a4171;
color: rgba(255, 255, 255, 0.61);
text-align: center;
border: 2px solid rgba(255, 255, 255, 0.555);
box-shadow: rgb(0 0 0 / 60%) 1px 3px 2px;
border-radius: 50px;
cursor: pointer;
transition: 300ms;
}

.selectedCategory{
border: 2px solid rgb(255, 255, 255);
box-shadow: rgb(255, 255, 255) 0px 0px 15px;
}


.switchPressureButtons{
display: flex;
flex-direction: row;
justify-content: space-evenly;
width: 80%;
margin: 4vh 0;

}

.switchPressureButton{
padding: 1vh 2vw;
font-size: 1.3vw;
background-color: #3e3d3f;
color: rgb(255, 255, 255);
text-align: center;
border: 2px solid rgb(139, 139, 139);
box-shadow: rgb(0 0 0 / 60%) 1px 3px 2px;
border-radius: 50px;
cursor: pointer;

&:active{
transform: scale(0.98);
transition: scale 300ms;
}
}

.selectedPressureCategory{
border: 2px solid rgb(255, 255, 255);
box-shadow: rgb(255, 255, 255) 0px 0px 15px;
}

.switchesContainer{
display: flex;
border: 2px solid rgb(139, 139, 139);
flex-direction: row;
justify-content: space-evenly ;
border-radius: 50px;
background-color: #3e3d3f;
padding: 0 1vw 0 3vw;
margin: 2vh 0;

.switchLabel{
margin-right: 1vw;
font-size: 1.1vw;
}
}

